.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #c9cdce;
}

.select-image-lable {
  box-sizing:border-box;
}
.select-image{
  display:inline-block;
  background-color: #b5b5b5;
  padding: 20px 40px;
  margin: 10px;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
  cursor: pointer;
  outline: none;
  transition: 0.2s all;
  
}
.select-image:hover{
    background-color: #8a8c8c;
  }

  .select-image:active{
    transform: scale(0.98);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  margin: 2px 5px;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  max-width: 300px;
  max-height: 100%;
  
}
.inline-figure{
  display: inline-block;
  margin: 5px 20px;
}
#image-name{
  font-size:x-small;
}
.inline-figure div{
  
  
  max-width: 300px;
  max-height: 300px;
}

.step{
  border:#282c34 solid 1px;
  border-radius: 10px;
  background-color: #f6f6f6;

  text-align: left;
  padding: 5px 10px;
  width: 70%;
  margin: 10px auto;
}


h4{
  margin: 0;
}